<template>
  <div class="quotes-container">
    <transition name="fade appear" 
      enter-active-class="animated zoomIn"
      leave-active-class="animated bounceOutRight">
      <div v-show="show">
        <div class="quote-icon"><i class="las la-quote-right"></i></div>
        <div class="quote-panel">
          <div class="quote-text">{{quote.text}}</div>
          <div class="quote-author" v-html="quote.author?'-- '+quote.author:''"></div>
        </div>
      </div>
      </transition>
  </div>
</template>

<script>
// import Clock1 from './Clock1.vue';


export default {
  name: 'quotes',
  components: {
      // Clock1
  },
  data () {
    return {
        quote: {
          author: '',
          text: ''
        },
        show: false
    }
  },

  methods: {
    readTextFile: function(file)
    {
        var rawFile = new XMLHttpRequest();
        rawFile.open("GET", file, true);
        var app = this;
        rawFile.onreadystatechange = function ()
        {
            if(rawFile.readyState === 4)
            {
                if(rawFile.status === 200 || rawFile.status == 0)
                {
                    let allText = rawFile.responseText;
                    let lines = allText.split('\n');
                    let line = lines[Math.floor(Math.random()*lines.length)];
                    let lineParts = line.split('","');
                    if (lineParts.length >= 2) {
                      app.quote.author = lineParts[0].replace('"', '');
                      app.quote.text = lineParts[1].replace('"', '');;
                    }
                }
            }
        }
        rawFile.send(null);
    }
        
  },
  mounted: function () {
      this.readTextFile('quotes.csv');
      setTimeout(() => {
          this.show = true;
        }, 500);
  }
}
</script>

<style lang="scss">
//@import url('https://fonts.googleapis.com/css?family=Great Vibes');

.quotes-container {
    width: 420px;
    float: right;
    max-width: 100%;
      position: relative;
      
    .quote-icon {
      font-size: 8em;
      color: #2c3e5073;
      position: absolute;
      top: -48px;
      left: -28px;
    }
    
    .quote-panel {
      position: absolute;
    }
    
  @media (max-width: 500px) {
    transform: scale(0.75);
    transform-origin: right top;
  } 
  .quote-text {
    font-family: Dosis, Avenir, Helvetica, Arial, sans-serif;
    font-size: 28px;
    color: #FFF;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px 5px #333333ba;
  }  
  
  .quote-author {
    color: #FFF;
    text-align: right;
    text-shadow: 2px 2px 5px #333333ba;
  }
}

</style>



